import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import { Bar, Line } from "react-chartjs-2";
import * as constants from "../../constants/Constants";
import { Col, Row, Card, CardBody, CardHeader } from "reactstrap";
import { CustomTooltips } from "@coreui/coreui-plugin-chartjs-custom-tooltips";
import Cards from "../../components/Cards/Cards";
class Dashboard extends Component {
  render() {
    return (
      <div className="animated fadeIn">
        <div className="layoutMargin">
        <Row>
          <Col xs="3">
            <div className="text-value">
              <i className="fa fa-align-justify" /> Media
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-warning text-center"
              label="Audio"
              value="105"
              icon="fa fa-headphones"
            />
          </Col>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-primary text-center"
              label="Video"
              value="80"
              icon="fa fa-video-camera"
            />
          </Col>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-secondary text-center"
              label="Images"
              value="250"
              icon="fa fa-picture-o"
            />
          </Col>
        </Row>
        <Row>
          <Col xs="3">
            <div className="text-value">
              <i className="fa fa-align-justify" /> Beneficiaries
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs="12" md="4">
            <Cards
              className="text-white bg-theme text-center"
              label="Beneficiaries"
              value="400"
              icon="fa fa-users"
            />
          </Col>
          <Col xs="12" md="4">
            <Cards
              className="text-white bg-primary text-center"
              label="Field Workers"
              value="100"
              icon="fa fa-user-secret"
            />
          </Col>
        </Row>

        <Row>
          <Col xs="3">
            <div className="text-value">
              <i className="fa fa-align-justify" /> Masters
            </div>
          </Col>
        </Row>
        <Row>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-primary text-center"
              label="States"
              value="3"
              icon="fa fa-globe"
            />
          </Col>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-warning text-center"
              label="Districts"
              value="45"
              icon="fa fa-compass"
            />
          </Col>
          <Col xs="12" md="3">
            <Cards
              className="text-white bg-theme text-center"
              label="Villages"
              value="56"
              icon="fa fa-map-marker"
            />
          </Col>
          {/* <Col xs="12" md="3">
            <Cards
              className="text-white bg-secondary text-center"
              label="Grampanchayats"
              value="40"
            />
          </Col> */}
        </Row>
      </div>
      </div>
    );
  }
}
export const mapStateToProps = state => {
  return {
    message: state.dashboard.message
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    getMessage: () => dispatch(actions.getMessage())
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);



